.mapDiv {
  padding: 0;
  margin: 0;
  height: 100%;
  width: 100%;
}

b {
  color: orange;
}

br {
  content: '';
  margin: 2em;
  display: block;
  font-size: 24%;
}

.calcitePanelBasemaps {
  display: grid;
  width: 100%;
  height: 100%;
}

/*https://developers.arcgis.com/calcite-design-system/foundations/colors/ */

calcite-label {
  --calcite-ui-text-1: #ffffff;
  --calcite-font-size--1: 1.1rem;
  /* margin-top: 1vh; */
  margin-left: 15px;
  margin-top: 5px;
}

calcite-action-bar {
  --calcite-ui-border-3: #555555;
  --calcite-ui-text-3: #bfbfbf;
}

calcite-shell {
  --calcite-ui-foreground-1: #2b2b2b;
  --calcite-ui-text-1: #ffffff;
  --calcite-ui-background: #2b2b2b;
  --calcite-ui-border-3: #555555;
}

calcite-shell-panel {
  --calcite-ui-foreground-1: #2b2b2b;
  --calcite-ui-foreground-2: #2b2b2b;
  --calcite-ui-border-3: #555555;
}

calcite-panel {
  overflow-y: auto;
  --calcite-ui-foreground-1: #2b2b2b;
  --calcite-ui-foreground-2: #2b2b2b;
  --calcite-ui-text-2: #ffffff;
  background-color: #2b2b2b;
  --calcite-ui-border-3: #555555;
}

calcite-list {
  overflow-y: auto;
  /* --calcite-ui-border-3: #555555; */
}

calcite-avatar {
  margin-inline-end: 0.3rem;
  margin-top: 0.15rem;
}

calcite-tabs {
  --calcite-ui-border-3: #555555;
  --calcite-ui-border-2: #555555;
  --calcite-ui-border-1: #555555;
  border-style: solid;
  border-width: 0.7;
  border-top: rgb(0, 0, 0, 0);
  width: 50vh;
}

#result-list {
  overflow-y: auto;
  --calcite-ui-text-1: #ff0000;
  --calcite-ui-text-3: #eaeaea;
  --calcite-ui-foreground-2: #202020;
}

#exproListChip {
  --calcite-ui-text-1: #ffffff;
  /*--calcite-ui-foreground-2: #555555;*/
  --calcite-ui-foreground-2: rgb(0, 0, 0, 0);
  margin-inline-end: 0.75rem;
}

.esri-view .esri-view-surface--touch-none:focus::after {
  outline: none !important;
}

.esri-icon-non-visible::before {
  content: '\e610' !important;
}
.esri-icon-visible::before {
  content: '\e611' !important;
  background-color: #00c5ff;
  color: #202020;
}

::-webkit-scrollbar {
  width: 5px;
  display: none;
}

.esri-legend__layer-caption {
  display: none;
}

.calcite-tutorial {
  --calcite-ui-brand: #039851;
  --calcite-ui-brand-hover: #008d52;
}

.calcite-tutorial calcite-chip {
  --calcite-ui-foreground-2: var(--calcite-ui-brand);
  --calcite-ui-text-1: white;
  margin-inline-end: 0.75rem;
}

/* Lot Tab */

.totalLotsLabel {
  color: white;
  font-size: 1.1vw;
  padding-bottom: 0.5vh;
  padding-top: 0.6vh;
}

.totalLotsNumber,
.handedOverNumber {
  color: #6ede00;
  font-size: 2rem;
  font-weight: bold;
  font-family: 'calibri';
  margin-left: 4%;
}

.totalLotsNumber2 {
  color: white;
  font-size: 1vw;
  font-weight: normal;
}

.handedOverLabel {
  color: white;
  font-size: 1.1vw;
  padding-bottom: 0.6vh;
}

/* Structure Tab */
.totalStructuresLabel {
  color: white;
  font-size: 1vw;
  padding-bottom: 0.5vh;
  padding-top: 0.6vh;
}

.structureNumberImage {
  display: flex;
  padding-left: 20px;
}

.totalStructuresLabel {
  color: white;
  font-size: 1.1vw;
  padding-bottom: 0.5vh;
  padding-top: 0.6vh;
}

.DemolishedNumber {
  color: #6ede00;
  font-size: 2rem;
  font-weight: bold;
  font-family: 'calibri';
  margin-left: 4%;
}

.moaStructureTitle {
  color: white;
  font-size: 1.1vw;
  padding-bottom: 0.5vh;
  margin-top: 4vh;
  padding-left: 20px;
}

/* ISF Tab */
.isfNumberImage {
  display: flex;
  padding-left: 20px;
}

.totalIsfLabel {
  color: white;
  font-size: 1.1vw;
  padding-bottom: 0.5vh;
  padding-top: 0.6vh;
}

.isfTotalNumber {
  color: #6ede00;
  font-size: 2rem;
  font-weight: bold;
  font-family: 'calibri';
  margin-left: 4%;
}

/* Header */
.headerTitle {
  color: white;
  margin-left: 1rem;
  font-size: 1.4rem;
  margin-top: auto;
  margin-bottom: auto;
}

.date {
  color: rgb(220, 220, 220);
  font-size: 0.8rem;
  width: 150px;
  height: 20px;
  margin-top: auto;
  margin-left: auto;
}

.dropdownFilter {
  height: 50px;
  width: 70%;
  border-color: rgb(0, 0, 0, 0);
  padding-top: 10px;
  margin-left: -10rem;
}

.dropdownFilterLayout {
  display: flex;
  flex-direction: row;
  font-size: 12px;
  color: black;
  justify-content: right;
}

/* Calcite Action panel */
.informationDiv {
  color: #eaeaea;
  font-size: 13px;
  margin-left: -10px;
  padding-right: 5px;
}

/* Progress Chart */
calcite-label {
  font-size: large;
}
